
#video,
      canvas,
      #overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      .mainContainer {
        position: absolute;
        width: 100%;
        top: 50px;
        left: 0px;
        z-index: 100;
        height: 50px;
        display: flex;
        justify-content: center;
        gap: 20px;
      }

      .redirectionBtn {
        position: absolute;
        width: 50px;
        height: 50px;
        bottom: 10px;
        right: 10px;
        z-index: 100;
        border-radius: 50%;
        border: none;
        background-color: #ffbf00;

        display: flex;
        justify-content: center;
        align-items: center;
      }

      .mainContainer button {
        background: black;
        color: white;
        border: none;
        border-radius: 10px;
        padding: 10px 20px;
        font-size: 16px;
        font-weight: bold;
        cursor: pointer;
      }

      .loaderContainer {
        position: absolute;
        /* width: 100%;
			height: 100vh; */
        width: 64px;
        height: 68px;
        top: 50%;
        left: 50%;
        margin-left: calc(-64px / 2);
        margin-top: calc(-68px / 2);
        /* transform: translate(40%, 50%); */
        z-index: -2;
      }

      #staticImg1 {
        position: absolute;
        width: 345px;
        height: 600px;
        top: 54%;
        left: 50%;
        margin-left: calc(-354px / 2);
        margin-top: calc(-600px / 2);
      }

      #video01 {
        position: absolute;
        width: 345px;
        height: 600px;
      }

      .container{
        position: absolute;
        /* width: 100%;
        height: 100%; */
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
      }
